import { HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { Observable } from 'rxjs';
import { Inject, Injectable } from '@angular/core';
import { ORIGIN_URL } from '@nguniversal/aspnetcore-engine/tokens';

const API_URL = environment.apiUrl;
@Injectable({
  providedIn: 'root'
})
export class SuperService implements ISuperService {
  // @Inject(ORIGIN_URL) originUrl: string;
  public url = `${this.originUrl}/api` || environment.apiUrl;
  public controller = '';
  constructor(protected http: HttpClient, @Inject(ORIGIN_URL) protected originUrl: string) {
    console.log(`url: ${this.url}, asp: ${originUrl}/api, env: ${environment.apiUrl}`);
  }

  getList(startIndex = 0, pageSize = 6) {
    return this.http.get(`${API_URL}/${this.controller}/GetList/${startIndex}/${pageSize}`);
  }
  get = () => this.http.get(`${API_URL}/${this.controller}/get`);
  getOne = (id) => this.http.get(`${API_URL}/${this.controller}/get/${id}`);
  post = (o) => this.http.post(`${API_URL}/${this.controller}/post`, o);
  put = (o) => this.http.put(`${API_URL}/${this.controller}/put/${o.id}`, o);
  delete = (id) => this.http.delete(`${API_URL}/${this.controller}/delete/${id}/`);
}

interface ISuperService {
  getList(startIndex, pageSize): Observable<any>;
  get();
  getOne(id);
  post(o);
  put(o);
  delete(id);
}
