import { Injectable, PLATFORM_ID, Inject } from '@angular/core';
import { Router } from '@angular/router';
import { User } from './models';
import { isPlatformBrowser } from '@angular/common';

const KEY_USER = 'USER_MAIN_PROJECT';
@Injectable({
  providedIn: 'root'
})
export class SessionService {
  public _user = new User();
  // public user: User;
  // public reader: Reader;
  // public antennas: Antenne[];
  // public isReaderConnected = false;

  constructor(@Inject(PLATFORM_ID) protected platformId: Object) {
    this.getSession();
  }

  get user() {
    // this.getSession();
    // console.log(this._user);
    return this._user;
  }


  // se connecter
  public doSignIn(user) {
    if (!user) {
      return;
    }
    this._user = user;
    if (isPlatformBrowser(this.platformId)) {
      localStorage.setItem(KEY_USER, btoa(JSON.stringify(this._user)));
    }
  }

  // se deconnecter
  public doSignOut(): void {
    // remove user from local storage to log user out
    this._user = new User();
    if (isPlatformBrowser(this.platformId)) {
      localStorage.removeItem(KEY_USER);
    }
  }

  // this methode is for our auth guard
  get isSignedIn(): boolean {
    if (isPlatformBrowser(this.platformId)) {
      return !!localStorage.getItem(KEY_USER);
    }
  }

  //
  public getSession(): void {
    if (isPlatformBrowser(this.platformId)) {
      if (localStorage.getItem(KEY_USER)) {
        this._user = JSON.parse(atob(localStorage.getItem(KEY_USER)));
      }
    }
  }
}

