export class User {
    id = 0;
    fullName = '';
    imageUrl = '';
    address = '';
    email = '';
    password = '';
    tel = '';
    idTypeUser = 0;
    typeUser = new TypeUser();
    userRoles: UserRole[] = [];
    adverts: Advert[] = [];
}

export class TypeUser {
    id = 0;
    name = '';
    users: User[] = [];
}

export class UserRole {
    idUser = 0;
    idRole = 0;
    user = new User();
    role = new Role();
}

export class Advert {
    id = 0;
    idUser = 0;
    idModel = 0;
    dateAdvert = new Date();
    dateSell = new Date();
    state = false;
    user = new User();
    model = new Model();
}

export class Marque {
    id = 0;
    name = '';
    imageUrl = '';
    idCountry = 0;
    country = new Country();
    models: Model[] = [];
}

export class Country {
    id = 0;
    name = '';
    imageUrl = '';
    models: Model[] = [];
}

export class Model {

}

export class Transmission {
    id = 0;
    name = '';
}

export class Role {
    id = 0;
    name = '';
}


